:root {
  --chat-background-color: get_option("chat_background_color", "#fafafa");
  --chat-header-background: get_option("chat_header_background", "#333333");
  --chat-header-text-color: get_option("chat_header_text_color", "#ffffff");
  --chat-header-status-color: get_option("chat_header_status_color", "#d1d1d1");
  --chat-message-background: get_option("chat_message_background", "#ffffff");
  --bot-message-background: get_option("bot_message_background", "#aaaaaa");
  --bot-message-text-color: get_option("bot_message_text_color", "#333333");
  --user-message-background: get_option("user_message_background", "#0095f6");
  --user-message-text-color: get_option("user_message_text_color", "#cccccc");
  --button-background: get_option("button_background", "#0095f6");
  --button-text-color: get_option("button_text_color", "#ffffff");
  --bot-avatar-image: url(get_option("bot_avatar_image" "../assets/images/avatar-img.jpeg"));
  --inputs-background: get_option("inputs_background", "#dfdfdf");
}

.wrap-mkt {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}
.wrap-mkt h2 {
  font-size: 24px;
  margin-bottom: 20px;
}
.wrap-mkt form fieldset {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 20px;
  margin-bottom: 10px;
}
.wrap-mkt form fieldset legend {
  font-size: 18px;
  font-weight: bold;
}
.wrap-mkt form fieldset .input-group {
  margin-bottom: 20px;
}
.wrap-mkt form fieldset .input-group label {
  display: inline-block;
  width: 150px;
  font-weight: bold;
}
.wrap-mkt form fieldset .input-group input[type=text],
.wrap-mkt form fieldset .input-group select {
  width: calc(100% - 160px);
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}
.wrap-mkt form .choices-container .add-choice {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.wrap-mkt form .choices-container .chat-choice {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}
.wrap-mkt form .choices-container .chat-choice input[type=text] {
  flex: 1;
  margin-right: 10px;
}
.wrap-mkt form .choices-container .chat-choice .remove-choice {
  background-color: #f44336;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.wrap-mkt form .next-question-container .add-conditional {
  background-color: #4caf50;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.wrap-mkt form .next-question-container .conditional-container {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}
.wrap-mkt form .next-question-container .conditional-container select,
.wrap-mkt form .next-question-container .conditional-container input[type=text] {
  flex: 1;
  margin-right: 10px;
}
.wrap-mkt form .next-question-container .conditional-container .remove-conditional {
  background-color: #f44336;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.wrap-mkt form .remove-question {
  background-color: #f44336;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 20px;
}
.wrap-mkt form #add-question {
  background-color: #4caf50;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 20px;
}
.wrap-mkt form .form-footer {
  margin-top: 20px;
}
.wrap-mkt form .form-footer input[type=submit] {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.wrap-mkt .wrap-theme {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}
.wrap-mkt .theme-settings-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.wrap-mkt .theme-settings-header {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  border-bottom: 1px solid #ccc;
  padding-bottom: 10px;
}
.wrap-mkt .theme-settings-group {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}
.wrap-mkt .theme-setting {
  display: flex;
  flex-direction: column;
}
.wrap-mkt .theme-setting-label {
  font-size: 1rem;
  margin-bottom: 0.5rem;
}
.wrap-mkt .theme-setting-input {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}
.wrap-mkt input[type=submit] {
  padding: 10px 20px;
  background-color: #0095f6;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}